import React, { Component } from 'react'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";

import Home from './Home.js'
import SignIn from './SignIn.js'
import SearchPage from './SearchPage.js'

export class App extends Component {
    render() {
        return (
            <div>
                <Router>
                    <Switch>
                        <Route exact path="/">
                            <Home />
                        </Route>
                        <Route path="/sign-in">
                            <SignIn />
                        </Route>
                        
                
                    </Switch>
                </Router>
            </div>
        )
    }
}

export default App
